.user .title {
  display: grid;
  grid-template-columns: 60px auto;
  grid-template-rows: 30px auto;
  grid-template-areas: "img h3" "img .";
  margin: 20px 0;
  grid-column-gap: 1em;
}
.user .title img {
  width: 60px;
  height: 60px;
  grid-area: img;
}
.user .title h3 {
  font-size: 16px;
  line-height: 23px;
  grid-area: h3;
  font-weight: normal;
}
.user hr {
  height: 0;
  margin: 15px 0;
  overflow: hidden;
  background: transparent;
  border: 0;
  border-bottom: 1px solid #ddd;
}
.user .indexBlog-wrapper {
  display: grid;
  grid-template-columns: 236px auto;
  grid-template-rows: auto auto;
  grid-template-areas: "day h3" "year p";
  grid-column-gap: 1em;
  font-size: 14px;
  color: #000;
  margin-top: 50px;
}
.user .indexBlog-wrapper .day span,
.user .indexBlog-wrapper .year-month span {
  display: block;
  color: #666;
  text-align: center;
  font-size: 13px;
}
.user .indexBlog-wrapper .day span.day,
.user .indexBlog-wrapper .year-month span.day {
  font-size: 22px;
  grid-area: day;
}
.user .indexBlog-wrapper h3 {
  grid-area: h3;
  font-weight: normal;
  font-size: 16px;
}
.user .indexBlog-wrapper h3 span {
  font-size: 12px;
  line-height: 18px;
  color: #999;
  margin-left: 20px;
}
.user .indexBlog-wrapper p {
  grid-area: p;
  line-height: 16px;
}
.user .actions {
  grid-column: 2;
  grid-row: 3;
  font-size: 12px;
  margin-left: 50px;
}
.user .actions a {
  color: #009933;
}
.user .pagination {
  display: grid;
  justify-items: center;
  margin: 20px 0;
}
