.create {
  position: relative;
}
.create h2 {
  font-size: 20px;
  line-height: 28px;
  font-weight: normal;
  text-align: center;
  margin: 20px 0;
}
.create h3 {
  font-size: 16px;
  line-height: 23px;
  font-weight: normal;
  margin-bottom: 15px;
  margin-top: 25px;
}
.create .position-span {
  font-size: 12px;
  line-height: 18px;
  color: #999;
  position: absolute;
  right: 0;
}
.create input {
  margin-bottom: 10px;
}
.create .is-index {
  margin-top: 8px;
}
.create .button {
  background-color: #439641;
  color: white;
  font-size: 16px;
  margin-top: 20px;
}
