.login-register {
  display: -webkit-flex;
  display: flex;
  justify-items: center;
  -webkit-align-content: center;
          align-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding-top: 30px;
}
.login-register .wrapper {
  margin: 0 auto;
}
.login-register h4 {
  margin: 10px 0 5px;
}
.login-register .p {
  height: 20px;
  font-size: 12px;
  color: #f00;
  margin: 5px 0;
}
.login-register input {
  width: 400px;
}
.login-register button {
  margin-top: 10px;
  width: 120px;
  font-weight: normal;
}
.login-register .notice {
  font-size: 12px;
  color: #999;
  text-align: center;
  margin-top: 30px;
}
.login-register .notice a {
  color: #009933;
}
