.app {
  min-height: 100vh;
  background-color: #fff;
}
.app .header {
  background-color: #009933;
  padding: 0 12%;
  height: auto;
  line-height: normal;
}
.app .header .no-login {
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  color: #fff;
  padding-bottom: 30px;
}
.app .header .no-login h1 {
  color: #fff;
  font-size: 40px;
  margin: 60px 0 0;
  text-transform: uppercase;
}
.app .header .no-login p {
  margin: 15px 0 0;
}
.app .header .no-login .btns {
  margin-top: 20px;
}
.app .header .no-login button {
  margin: 20px 5px 0;
}
.app .header .login {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  background-color: #009933;
  color: #fff;
  padding: 8px 0;
}
.app .header .login a {
  color: white;
}
.app .header .login h1 {
  font-size: 40px;
  text-transform: uppercase;
  margin-bottom: 0;
  -webkit-flex: 1 1;
          flex: 1 1;
}
.app .header .login .edit {
  cursor: pointer;
  font-size: 30px;
}
.app .header .login .avatar {
  width: 40px;
  height: 40px;
  border: 1px solid #fff;
  border-radius: 50%;
  margin-left: 15px;
}
.app .header .login .user {
  position: relative;
}
.app .header .login .user ul {
  z-index: 999;
  display: none;
  background-color: #fff;
  position: absolute;
  right: 0;
  border: 1px solid #eaeaea;
}
.app .header .login .user ul span,
.app .header .login .user ul a {
  color: #333;
  font-size: 12px;
  display: block;
  padding: 5px 10px;
  cursor: pointer;
}
.app .header .login .user ul span:hover,
.app .header .login .user ul a:hover {
  background-color: #eaeaea;
}
.app .header .login .user:hover ul {
  display: block;
}
.app .main {
  padding: 0 12%;
}
.app .footer {
  height: auto;
  background-color: #d7d7d7;
  color: #666;
  font-size: 13px;
  text-align: center;
}
.app .footer p {
  margin-bottom: 0;
  line-height: 13px;
}
