.index .index-blog-wrapper {
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: auto auto;
  grid-template-areas: "img h3" "img p";
  grid-column-gap: 6em;
  font-size: 14px;
  color: #000;
  margin-top: 50px;
}
.index .index-blog-wrapper .img-wrapper {
  grid-area: img;
}
.index .index-blog-wrapper .img-wrapper img {
  width: 60px;
  height: 60px;
}
.index .index-blog-wrapper .img-wrapper span {
  display: block;
  font-size: 12px;
  line-height: 18px;
  color: #999;
  text-align: left;
  margin-top: 10px;
}
.index .index-blog-wrapper h3 {
  grid-area: h3;
  font-weight: normal;
  font-size: 16px;
}
.index .index-blog-wrapper h3 span {
  font-size: 12px;
  line-height: 18px;
  color: #999;
  margin-left: 20px;
}
.index .index-blog-wrapper p {
  grid-area: p;
  line-height: 16px;
}
.index .pagination {
  margin: 20px 0;
}
